
import Push from "push.js";
export default {
  name: "PushCom",
  components: {},
  setup() {
    const example1 = (msg: string) => {
      Push.create(msg);
    };
    const example2 = (msg: string, body: string) => {
      Push.create(msg, {
        timeout: 4000,
        body,
        icon: "favicon.ico",
        onClick() {
          console.log("通知被打开");
          window.focus();
        },
      });
    };
    const example3 = () => {
      Push.create("推送三", {
        tag: "push3",
      });
    };
    const clearExample3 = () => Push.close("push3");
    const example4 = () => Push.clear();
    return {
      example1,
      example2,
      example3,
      clearExample3,
      example4,
    };
  },
};
